<template>
  <transition v-if=" !loading" name="fade">
    <div id="wrapper">
        <sidebar :name="user.name" :page="'Admin'" :sidebar_data=sidebar_data></sidebar>
        
        <div class="body">
          <div class="backdrop" v-on:click="close()"></div>
            
          <admin-header :title="'Manage Site'" :tagline="'All your controls, in one place.'" v-model:all_screens=all_screens v-model:screen=screen v-model:url=url  style="margin-bottom: 3rem;"></admin-header>

          <div class="container mb-4">
            <div class="content-wrapper">
              <div class="content mx-auto">

                <!--  List 1 -->
                <auto-list v-if="url != null" v-model:all_screens=all_screens v-model:screen="screen" :title=title :url="url" v-model:list_data="list_data"></auto-list>

              </div>                                          
            </div>
          </div>
          
          <footer class="main-footer d-flex p-4 pb-2">
            <div class="float-end mx-auto">
              Copyright &copy;  

              Built on <a href="www.inshaka.ga">Inshaka</a>
            </div>
          </footer>
        </div>

        <property-modal></property-modal>

        <amenity-modal></amenity-modal>

        <images-modal></images-modal>

        <user-modal></user-modal>
    </div>
  </transition>
</template>

<style lang="scss" scoped>
  @import '../assets/scss/layout/_spinner';
</style>

<script>
import Sidebar from '../components/AutoSidebar'
import AdminHeader from '../components/Header'
import AutoList from '../components/AutoList'
import PropertyModal from '../components/Custom/PropertyModal';
import AmenityModal from '../components/Custom/AmenityModal';
import ImagesModal from '../components/Custom/ImagesModal';
import UserModal from '../components/Custom/UserModal';

export default {
  name: 'Admin',
  components: {
    AdminHeader,
    Sidebar,
    AutoList,
    PropertyModal,
    AmenityModal,
    ImagesModal,
    UserModal
  },
  data() {
    return {
      url: 'api/users',
      title: {status: true, title: 'Your Data', actions: [
        {name: 'Add User', type: 'modal', action_url: 'user'},
        {name: 'Add Property', type: 'modal', action_url: 'property'}
      ]},
      sidebar_data: [
        {title: 'Admin', link: '/admin', if_children: false, icon: 'cog', modal: false},
        {title: 'Edit Site', link: '', if_children: false, icon: 'home', modal: false},
        {title: 'Logout', link: 'logout', if_children: false, icon: 'sign-out-alt', modal: false},
      ],
      all_screens: [
        {title: 'Users', screen: 1, url: 'api/users', actions: [
          {name: 'Edit', type: 'modal', action_url: 'user'},
          {name: 'Delete', type: 'api', action_url: {type: 'delete', url:'api/users/', message: {success: 'Success!', error: 'Error!'}}},
        ]},
        {title: 'Property', screen: 2, url: 'api/property', actions: [
          {name: 'Edit', type: 'modal', action_url: 'property'},
          {name: 'Delete', type: 'api', action_url: {type: 'delete', url:'api/property/', message: {success: 'Success!', error: 'Error!'}}},
          {name: 'Featured', type: 'api', action_url: {type: 'post', url:'api/featured/', message: {success: 'Featured update!', error: 'Error!'}}},
          {name: 'Manage Images', type: 'modal', action_url: 'images'},
          {name: 'Manage Amenities', type: 'modal', action_url: 'amenity'},
        ]},
      ],
      screen: 1,
      list_data: [],
      loading: true,
    } 
  },
  watch: {
    user: function() {
      if( !this.user) {
        const user = localStorage.getItem('user')
        if(user) {
          localStorage.removeItem('user')
        }

        this.$router.push({name: 'Home'});
      }

      this.loading = false
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    }
  },      
  mounted() {
    this.$store.dispatch("getUser")
  },
}
</script>
