<template>
        <!-- Modal -->
    <div class="modal fade" id="images" tabindex="-1" role="dialog" aria-labelledby="imagesLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable" role="document">
            <div class="modal-content" style="overflow-y: scroll">
                <div class="modal-header pb-0">
                    <span class="d-block">
                        <h5 class="content-title-min">Images</h5>
                        <p class="text-muted">Upload Image Details</p>
                    </span>
                </div>

                <form action="#" @submit.prevent="handleSubmit()" enctype="multipart/form-data">

                    <status v-model:status_data=status_data></status>
                    
                    <span v-if="screen == 1">
                        <div class="modal-body pt-0">
                            <div class="form-row mt-3">
                                <label for="image" class="fw-bold mb-2">Property Images</label>

                                <div class="input-group">                     
                                    <input id="image" type="file" class="form-control" required @change="imageEvent($event)">
                                </div>
                                
                                <small>Upload Image</small>
                            </div>

                            <div class="form-row mt-3">
                                <label for="address" class="fw-bold mb-2">Caption</label> 
                                <div class="input-group">
                                    <input v-model="form_data.caption" id="name" type="text" class="form-control" placeholder="Label Image Name." required>
                                </div>
                            </div>

                            <div class="form-row mt-4 d-flex">
                                <button type="submit" class="btn btn-dark text-white w-75 me-2">
                                    Save
                                </button>  

                                <button type="button" class="btn btn-secondary w-25" @click="changeScreen(2)">
                                    Next <font-awesome-icon class="ms-2" :icon="['fas', 'angle-right']"></font-awesome-icon>
                                </button>
                            </div>
                        </div> 
                    </span> 

                    <span v-if="screen == 2">
                        <div class="modal-body pt-0">
                            <div class="row">
                                <div class="col-md-6 col-sm-12 mb-4" v-for="({image_url, caption, id}, index) in property.images" :key="index">
                                    <!-- <img :src="'http://localhost:8000/' + image_url" :alt="caption" class="img-fluid rounded"> -->
                                    <img :src="'https://api.kipekee.co.ke/' + image_url" :alt="caption" class="img-fluid rounded">
                                    <div class="d-flex mt-2">
                                        <button  v-if="image_url != property.image_url"  class="btn btn-sm burgundy w-50 me-2" @click="deleteImage(id)">
                                            Delete <font-awesome-icon class="ms-2" :icon="['fas', 'trash-alt']"></font-awesome-icon>
                                        </button>

                                        <button v-if="image_url != property.image_url" class="btn btn-sm btn-dark w-50" @click="mainImage(image_url)">Set to Main Image</button>

                                        <button  v-if="image_url == property.image_url"  class="btn btn-sm burgundy w-75 me-2" @click="deleteImage(id)">
                                            Delete <font-awesome-icon class="ms-2" :icon="['fas', 'trash-alt']"></font-awesome-icon>
                                        </button>

                                        <button v-if="image_url == property.image_url" class="btn btn-sm btn-success w-25" @click="mainImage(image_url)">
                                            <font-awesome-icon class="ms-2" :icon="['fas', 'star']"></font-awesome-icon>
                                        </button>
                                    </div>
                                </div>
                            </div>

                            <div class="form-row mt-4 d-flex">
                                <button type="button" class="btn btn-secondary w-25" @click="changeScreen(1)">
                                    <font-awesome-icon class="me-2" :icon="['fas', 'angle-left']"></font-awesome-icon> Previous
                                </button> 
                            </div>                            
                        </div> 
                    </span>                                  
                </form>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    @import '../../assets/scss/layout/_form.scss';
</style>

<script>
import Status from '../../components/Status';

export default {
    name: 'ImagesModal',    
    components: {
        Status
    },
    data() {
        return {
            property: [],
            form_data: {
                caption: null,
                image_url: null,
                property_id: this.property_id
            },            
            status_data: {
                "success": false,
                "success_message": 'Form Submission was successful!',
                "error": false,
                "error_message": 'There are some errors.',
                "loading": false
            },
            screen: 1
        }
    },
    computed: {
        modal_list () {
            return this.$store.state.modal_list
        }
    },     
    watch: {
        modal_list: function() {
            if(Object.keys(this.$store.state.modal_list).length > 0) {
                this.property = {
                    name: this.$store.state.modal_list.name,
                    description: this.$store.state.modal_list.description,
                    address: this.$store.state.modal_list.address,
                    price: this.$store.state.modal_list.price,
                    floor_area: this.$store.state.modal_list.floor_area,
                    bedrooms: this.$store.state.modal_list.bedrooms,
                    bathrooms: this.$store.state.modal_list.bathrooms,
                    featured: this.$store.state.modal_list.featured,
                    type: this.$store.state.modal_list.type,
                    sale_type: this.$store.state.modal_list.sale_type,
                    id: this.$store.state.modal_list.id,
                    images: this.$store.state.modal_list.property_images,
                    image_url: this.$store.state.modal_list.image_url
                }
            } else {
                this.property = this.$store.state.modal_list
            }
        }
    },    
    methods: {
        changeScreen(value) {
            this.screen = value
        },
        imageEvent(event) {
            this.form_data.image_url = event
        },
        handleSubmit() {
            this.status_data.loading = true
            let data = new FormData();

            data.append('image_url', this.form_data.image_url.target.files[0])
            data.append('caption', this.form_data.caption)
            data.append('property_id', this.property.id)

            this.axios.post('api/photos/property', data).then((response) => {
                this.status_data.loading = false
                this.status_data.success = true
                this.status_data.error = false
                this.status_data.success_message = response.data.message

                setTimeout(() => {
                    this.status_data.success = false
                }, 1000);
            }).catch((error) => {
                this.status_data.loading = false
                this.status_data.success = false
                this.status_data.error = true
                this.status_data.error_message = error.response.data.message
            })            
        },
        deleteImage(value) {
            this.axios.delete('api/photos/property/' + value).then((response) => {
                this.status_data.loading = false
                this.status_data.success = true
                this.status_data.error = false
                this.status_data.success_message = response.data.message

                setTimeout(() => {
                    this.status_data.success = false
                }, 1000);
            }).catch((error) => {
                this.status_data.loading = false
                this.status_data.error = true
                this.status_data.success = false
                this.status_data.error_message = error.response.data.message
            })             
        },
        mainImage(image_url) {
            let data = {
                image_url: image_url
            }

            this.axios.put('api/property/' + this.property.id, data).then((response) => {
                this.status_data.loading = false
                this.status_data.success = true
                this.status_data.error = false
                this.status_data.success_message = response.data.message

                setTimeout(() => {
                    this.status_data.success = false
                }, 1000);
            }).catch((error) => {
                this.status_data.loading = false
                this.status_data.error = true
                this.status_data.success = false
                this.status_data.error_message = error.response.data.message
            })             
        }            
    }
}
</script>